import * as React from "react";
import styled from "styled-components";
import * as faBrands from "styled-icons/fa-brands";

const Root = styled.a`
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
`;

const wrapIcon = icon => styled(icon)`
    margin-right: 5px;
    width: 20px;
    height: 20px;
`;

const ItchIoIcon = ({ className }) => (
    <svg className={className} aria-hidden="true" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 177 177">
        <path fill="currentColor"
              d="M23 0L0 27v7.7c0 9.8 9 18.4 17 18.4 9.9 0 18-8.3 18-18 0 9.7 7.9 18 17.6 18S70 44.8 70 35c0 9.7 8.4 18 18.1 18C98 53 106 44.8 106 35a18 18 0 0 0 17.7 18c9.7 0 17.3-8.3 17.3-18 0 9.7 8.4 18 18.2 18A18 18 0 0 0 177 35v-8L152 0C108.4-.1 23 0 23 0z"
              fillRule="evenodd"/>
        <path fill="currentColor"
              d="M35 48.6c-1 1.6-2.2 3-3.6 4.4-3.6 3.6-8.7 5.9-14.3 5.9-1.4 0-5-.8-5-.8v100h154V57.5c-2.4.9-4.3 1.4-7 1.4a20.3 20.3 0 0 1-17.7-10.4 20.6 20.6 0 0 1-17.7 10.4 20.3 20.3 0 0 1-17.8-10.4 20.4 20.4 0 0 1-35.5 0A20.4 20.4 0 0 1 38.3 53a19.5 19.5 0 0 1-3.3-4.4zM57.2 67c6.7 0 11.5 4.4 13.1 11h37.2c1.7-6.6 6.4-11 13.1-11 10.7 0 22.4 11 26 24.4l8.8 32c3.7 13.3-2 24.3-12.7 24.3-9.4 0-19.6-8.5-24.4-19.6H59.5c-4.9 11.1-15 19.6-24.4 19.6-10.7 0-16.4-11-12.8-24.4l8.8-32C34.8 78 46.5 67 57 67z"/>
        <path fill="currentColor" d="M75.7 95.4l-12.5 13.4h12.4v13.9h24.6v-13.9h13.2l-12.6-13.4-12.5-13.5z"/>
    </svg>
);


const icons = {
    github: wrapIcon(faBrands.Github),
    twitter: wrapIcon(faBrands.Twitter),
    facebook: wrapIcon(faBrands.Facebook),
    vk: wrapIcon(faBrands.Vk),
    itch: wrapIcon(ItchIoIcon)
};

class LinkWithIcon extends React.PureComponent {
    render() {
        const { icon: Icon, url, children } = this.props;

        return (
            <Root href={url} target="_blank">
                <Icon size={20}/> {children}
            </Root>
        );
    }
}

export const FacebookLink = ({ url, children }) => <LinkWithIcon url={url} icon={icons.facebook}>{children}</LinkWithIcon>;
export const TwitterLink = ({ url, children }) => <LinkWithIcon url={url} icon={icons.twitter}>{children}</LinkWithIcon>;
export const VkLink = ({ url, children }) => <LinkWithIcon url={url} icon={icons.vk}>{children}</LinkWithIcon>;
export const GithubLink = ({ url, children }) => <LinkWithIcon url={url} icon={icons.github}>{children}</LinkWithIcon>;
export const ItchIoLink = ({ url, children }) => <LinkWithIcon url={url} icon={icons.itch}>{children}</LinkWithIcon>;
