import React from "react";
import styled from "styled-components";

import { FacebookLink, GithubLink, TwitterLink, VkLink } from "./LinkWithIcon";
import { Cell } from "./Cell";
import { GithubProfileUrl } from "../consts";

const SocialLinks = styled.div`
    margin-top: 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
`;

const About = () => (
    <Cell>
        Full-time backend and web developer. Occasionally a spare-time game developer.

        <SocialLinks>
            <FacebookLink url={"https://facebook.com/0xc0dec"}>0xc0dec</FacebookLink>
            <TwitterLink url={"https://twitter.com/0xc0dec"}>0xc0dec</TwitterLink>
            <GithubLink url={GithubProfileUrl}>0xc0dec</GithubLink>
            <VkLink url={"https://vk.com/0xc0dec"}>0xc0dec</VkLink>
        </SocialLinks>
    </Cell>
);

export default About;